/*      Single Product Page      */

.single-product {
  .site-content {
    display: block;
    width: 1120px;
    margin: auto;

    .widget-area {
      display: none;
    }

    .site-main {
      margin: 4rem 0;

      .type-product {
        display: flex;
        justify-content: space-between;

        .header-wrapper {
          width: 48%;
          .entry-header {
            img {
              width: 100%;
              height: auto;
            }
          }
        }

        .product-content-wrapper {
          width: 48%;

          .entry-title {
            font-family: $font__heading;
            font-size: 2.5rem;
            text-transform: uppercase;
            letter-spacing: 2px;
            color: $color__brand-orange;
            margin-top: 0;
          }

          .entry-content {
            font-family: $font__accent;
            font-size: 2rem;

            p {
              font-family: $font__main;
              font-size: 1rem;
              line-height: 1.75;
            }
          }

          .social-links {
            button {
              background: $color__background-body;
              box-shadow: none;
              border: 1px solid $color__journal-read-more;
              border-radius: 0;
              padding: 0.75rem 1.5rem;
              text-transform: uppercase;
              font-family: $font__accent;
              font-size: 0.9rem;

              .fab {
                margin-right: 0.25rem;
              }
            }

            button:hover {
              background: $color__brand-green;
              color: $color__background-body;
              border: 1px solid $color__brand-green;
              text-shadow: none;
            }
          }
        }
      }

      .navigation {
        display: none;
      }
    }
  }
}

