/*      Single Journal Post Page      */

.cat-links,
.tags-links {
  font-family: $font__accent;
  text-transform: uppercase;
}

.navigation {
  display: none;
}

.social-links {
  button {
    background: $color__background-body;
    box-shadow: none;
    border: 1px solid $color__journal-read-more;
    border-radius: 0;
    padding: 0.75rem 1.5rem;
    text-transform: uppercase;
    font-family: $font__accent;
    font-size: 0.9rem;

    .fab {
      margin-right: 0.25rem;
    }
  }

  button:hover {
    background: $color__brand-green;
    color: $color__background-body;
    border: 1px solid $color__brand-green;
    text-shadow: none;
  }
}

.comments-area {
  .comment-list {
    .comment {
      .comment-body {
        margin-bottom: 2rem;
        padding-bottom: 2rem;
        border-bottom: 1px solid $color__comment-area;

        .comment-author-avatar {
          .avatar {
            border: 1px solid $color__background-body;
            border-radius: 35px;
          }
        }

        .comment-content {
          .comment-meta {
            .comment-author {
              .fn {
                font-family: 'Merriweather-Bold';
                font-style: normal;
              }
            }

            .comment-metadata {
              color: $color__brand-lightgrey;
            }
          }

          .reply {
            .comment-reply-link {
              font-family: $font__main;
              text-transform: none;
            }
          }
        }
      }
    }
  }

  .comment-respond {
    .comment-reply-title {
      font-family: $font__heading;
      font-size: 2rem;
      text-transform: uppercase;
      letter-spacing: 2px;
    }

    .comment-form {
      display: flex;
      flex-wrap: wrap;

      .comment-form-comment {
        display: flex;
        flex-direction: column;

        textarea {
          background: $color__comment-area;
          width: 800px;
        }
      }

      .comment-form-author {
        display: flex;
        flex-direction: column;
        width: 48%;
        margin-right: 1rem;

        input {
          background: $color__comment-area;
        }
      }

      .comment-form-email {
        display: flex;
        flex-direction: column;
        width: 48%;
        margin-left: 1rem;

        input {
          background: $color__comment-area;
        }
      }

      .comment-form-url {
        input {
          width: 800px;
          background: $color__comment-area;
        }
      }

      .form-submit {
        .submit {
          font-family: $font__accent;
          font-size: 0.9rem;
          text-shadow: none;
          text-transform: uppercase;
          background: $color__brand-green;
          box-shadow: none;
          border: $color__brand-green;
          border-radius: 0;
          padding: 0.75rem 1.5rem;
          color: $color__background-body;
          margin-bottom: 3rem;
        }

        .submit:hover {
          background: #1d625d;
        }
      }
    }
  }
}

/*      404 Page      */

.error404 {
  .site-content {
    .content-area {
      margin: 3rem 0 3rem 10rem;

      .not-found {
        .page-header {
          font-family: $font__heading;
          font-size: 1.25rem;
          text-transform: uppercase;
          letter-spacing: 2px;
          color: $color__text-main;
        }

        .page-content {
          .icon-search {
            display: none;
          }

          .search-form {
            fieldset {
              border: none;
              padding: 0;
              margin: 0;

              .search-submit {
                display: none;
              }

              .search-field {
                background: $color__comment-area;
                color: $color__text-main;
                text-transform: none;
                padding: 0.5rem 1.25rem;
                border-radius: 0;
              }
            }
          }

          .widget {
            .widgettitle,
            .widget-title {
              font-size: 1.5rem;
              letter-spacing: 2px;
            }

            ul {
              list-style: none;
              letter-spacing: 0;
              margin: 0;
              padding: 0;

              li {
                margin: 0.5rem 0;

                a {
                  font-family: $font__main;
                  margin: 0;
                  text-transform: none;
                }
              }
            }
          }
        }
      }
    }
  }
}

/*      Search Page      */

.search-results {
  .content-area {
    width: 800px;
    margin: 1rem 0 2rem 10rem;

    .page-header {
      .page-title {
        font-family: $font__heading;
        font-size: 2.5rem;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding-bottom: 2rem;
        border-bottom: 1px solid $color__comment-area;
      }
    }

    article {
      margin-bottom: 5rem;

      .entry-header {
        .entry-title {
          a {
            margin: 0;
            font-family: $font__heading;
            font-size: 2rem;
            letter-spacing: 2px;
          }
        }
      }

      .entry-summary {
        p {
          .read-more {
            color: $color__journal-read-more;
            padding: 0.75rem 1.25rem;
            margin: 0;
            border: 1px solid $color__journal-read-more;
          }

          .read-more:hover {
            color: $color__background-body;
            background: $color__brand-green;
            border: 1px solid $color__brand-green;
          }
        }
      }
    }
  }
}
